/* =========================
   CARD WIDTH CONTROL
========================= */
.event-card {
  width: 315px;
  flex-shrink: 0;
  cursor: pointer;
}

/* =========================
   IMAGE
========================= */
.event-img {
  height: 120px;
  object-fit: cover;
}

/* =========================
   SINGLE LINE ELLIPSIS
========================= */
.ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

/* =========================
   MULTI-LINE ELLIPSIS (2 lines)
========================= */
.ellipsis-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* =========================
   SMALL TEXT SIZE
========================= */
.text-sm {
  font-size: 13px;
}

/* =========================
   VIEW BADGE
========================= */
.view-badge {
  background: #f3f3f3;
  padding: 5px 14px;
  border-radius: 18px;
  font-size: 12px;
}
